<template>
  <div>
        
        <van-picker
        title="消费项目"
        :columns="list"
        @confirm="onConfirm"
        @change="onChange"
        />
  </div>
</template>

<script>
import { Toast } from 'vant';
import storage from '../router/storage';
export default {
    name:'Kinds',
    data() {
        return {
            list : [],
        };
    },
    methods: {
        onConfirm(value,index) {
            Toast(`当前值：${value}, 当前索引：${index}`);
        },
        onChange(picker,value,index) {
            Toast(`当前值：${value}, 当前索引：${index+1}`);
            this.$emit('change',value);
        },
        
    },
    mounted(){
        if(storage.get('lists') == null){
            console.log("找不到项目数据")
        }else{
            this.list = storage.get('lists');
            if(storage.get('project') == null){
                console.log("键值对为空");
            }else{
                var data = storage.get('project');
                if(data.delete == ''){
                    console.log("delete为空值");
                }else{
                    for(var i=0;i<this.list.length;i++){
                        if(this.list[i] == data.delete){
                            this.list.splice(i,1);
                        }
                        console.log('+++++++++')
                    }
                }
                var flag = true;
                if(data.add == ''){
                    console.log("add为空值");
                }else{
                    for(var j=0;j<this.list.length;j++){
                        if(data.add == this.list[j]){
                            flag = false;
                        }
                    }
                    if(flag){
                        this.list.push(data.add);
                    }else{
                        console.log('该项目已存在');
                    }
                }
                storage.set('lists',this.list);
                console.log(this.list);
            }
        }
    }
}
</script>

