import Vue from 'vue';
import Router from 'vue-router';
import Login from '../views/Login';
import Main from '../views/Main';
import Register from '../views/Register';
import Add from '../views/main/Add';
import Home from '../views/main/Home';
import Set from '../views/main/Set';
import App from '../App.vue';

Vue.use(Router);

export default new Router({
    routes:[
        {
            path:'/',
            name:'App',
            component: App,
            redirect:'/Login',
            children:[
                {
                    path:'/login',
                    name:'Login',
                    component: Login
                },
                {
                    path:'/register',
                    name:'Register',
                    component: Register
                },
                {
                    path:'/main',
                    name:'Main',
                    component: Main,
                    redirect:'/main/add',
                    children:[
                        {
                            path:'/main/home',
                            name:'Home',
                            component:Home
                        },
                        {
                            path:'/main/add',
                            name:'Add',
                            component:Add
                        },
                        {
                            path:'/main/set',
                            name:'Set',
                            component:Set
                        },
                    ]
                }
            ]
        },
    ]
})