<template>
  <div>
        <van-pull-refresh v-model="isLoading" success-text="刷新成功" @refresh="onRefresh">
            <ul>
                <li v-for="(list,index) in lists" :key="list.id"><item :user="list.user" :date="list.date" :item="list.item" :amount="list.amount" :kind="list.kind" :msg="list.msg" :index="index" /></li>
            </ul>
            <p>共有{{number}}记录,收入{{flag1}}项,支出{{flag2}}项</p>
        </van-pull-refresh>
  </div>
</template>

<script>
import storage from '../../router/storage.js'
import Item from '../../components/Item.vue'
export default {
    components: { Item },
    name:'Home',
    data(){
        return{    
            number:0,
            flag1:0,
            flag2:0,
            lists:[],
            userlists:[],
            isLoading: false,
        }
    },
    mounted(){
        if(storage.get('goods')==null){
            console.log("数据为空");
        }else{
            this.userlists = storage.get('goods');
            var user = storage.get('name');
            for(var j=0;j<this.userlists.length;j++){
                if(user == this.userlists[j].user){
                    this.lists.push(this.userlists[j])
                }
            }
            this.number = this.lists.length;
            for(var i=0;i<this.lists.length;i++){
                if(this.lists[i].kind == '1'){
                    this.flag1++;
                }else{
                    this.flag2++;
                }
            }
        }
    },
    methods: {
        onRefresh() {
        setTimeout(() => {
            this.isLoading = false;
            this.count++;
        }, 1000);
        },
    },
}
</script>

<style scoped>
p{
    width:100%;
    height: 80px;
    font-size: 16px;
    text-align: center;
    color: #C0C4CC;
}
</style>