<template>
  <div id="login">
        <van-form @submit="onSubmit" v-show="show==1">
            <div id="icon"><img src="../assets/login.png"></div>
            <van-field
                style="font-weight:bold;font-size:20px;"
                v-model="username"
                name="用户名"
                label="用户名"
                placeholder="用户名"
                :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
                style="font-weight:bold;font-size:20px;"
                v-model="password"
                type="password"
                name="密码"
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />
            <div id="remPwd">
                <input type="checkbox" @click="remember" :checked="isRem" style="width:18px;height:18px;"/>
                <span>记住密码</span>
            </div>
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">提交</van-button>
            </div>
            <div id="buttom">
                <p><router-link to="/register" style="color:black;">注册</router-link></p>
                <p @click="findPwd">找回密码</p>
            </div>
        </van-form>
        <van-form @submit="onSubmit2" v-show="show==2">
            <div id="icon"><img src="../assets/findPwd.png"></div>
            <van-field
            style="font-weight:bold;font-size:20px;"
                v-model="username2"
                name="请输入用户名"
                label="用户名："
                placeholder="请输入用户名"
                :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
            style="font-weight:bold;font-size:20px;"
                v-model="password2"
                name="您的密码"
                label="您的密码："
            />
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">找回密码</van-button>
            </div>
            <div id="buttom">
                <p @click="findLogin">返回登录</p>
                <p><router-link to="/register" style="color:black;">点击注册</router-link></p>
            </div>
        </van-form>
        
        
  </div>
</template>

<script>
    import { Dialog } from 'vant';
    import storage from '../router/storage.js';
    export default {
        name: "Login",
        data() {
            return {
                username: '',
                password: '',
                username2: '',
                password2: '',
                show: 1,
                name: '',
                isRem: false,
            };
        },
        methods: {
            onSubmit(values) {
                if(storage.get('userinfo')==null){
                    Dialog.alert({
                    message: '您还没有注册！',
                    theme: 'round-button',
                    }).then(() => {
                    // on close
                    });
                }else{
                    var data = storage.get('userinfo');
                    var flag = false;
                    for(var i = 0;i<data.length;i++){
                        if(this.username==data[i].username && this.password==data[i].password1){
                            flag = true;
                        }
                    }
                    if(flag){
                        this.$router.push({name:'Add',params:{username:this.username}});
                        storage.set('name',this.username);
                        var user = storage.get('userRem');
                        var columns= ['交通', '食品', '水电', '衣服', '旅游','应酬','医疗','资料'];
                        storage.set('lists',columns);
                        if(user != this.username){
                            this.isRem = !this.isRem;
                            this.remember();
                        }
                    }else{
                        Dialog.alert({
                        message: '⽤户名和密码错误！',
                        theme: 'round-button',
                        }).then(() => {
                        // on close
                        });
                    }
                    console.log('submit', values);
                }
            },
            remember(){
                this.name = this.username;
                storage.set('userRem',this.name);
                if(this.isRem){
                    var remaga = storage.get('isRem');
                    if(remaga == 'true'){
                        storage.set('isRem','false');
                        this.password = '';
                    }
                }else{
                    storage.set('isRem','true');
                    this.isRem = true;
                }
                
            },
            onSubmit2(){
                var data = storage.get('userinfo');
                var flag = false;
                for(var i=0;i<data.length;i++){
                    if(data[i].username==this.username2){
                        flag = true;
                        this.password2 = data[i].password1;
                    }
                }
                if(!flag){
                    Dialog.alert({
                        message: '您还没有注册，请先点击注册吧！',
                        theme: 'round-button',
                        }).then(() => {
                        // on close
                        });
                }
            },
            findPwd(){
                this.show = 2;
            },
            findLogin(){
                this.show = 1;
            }
        },
        mounted(){
            var isRem = storage.get('isRem');
            if(isRem == 'true'){
                this.isRem = true;
                var data = storage.get('userinfo');
                var userRem = storage.get('userRem');
                for(var i=0;i<data.length;i++){
                    if(data[i].username==userRem){
                        this.username = data[i].username;
                        this.password = data[i].password1;
                    }
                }
            }else{
                console.log('您没有进行过密码保存！')
            }
        }
    }
</script>

<style scoped>
#login{
    margin-top: 140px;
}
#remPwd{
    text-align: right;
    padding-right: 10px;
    display: flex;
    justify-content:flex-end;
}
#buttom{
    display: flex;
    justify-content: center;
}
#buttom p{
    width:100%;
    text-align: center;
    height: 80px;
    font-size: 18px;
    color: black;
}
#icon{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}
</style>