<template>
    <div>
        <div id="head">
            <van-radio-group v-model="radio" direction="horizontal">
                <van-radio name="1">收入</van-radio>
                <van-radio name="2">支出</van-radio>
            </van-radio-group>
        </div>
        <calender  @confirm="onConfirm" />
        <kinds @change="onChange" />
        <van-divider />
        <van-row style="text-align:center;">
            <van-col span="8" style="line-height:45px;font-size:18px">金 额</van-col>
            <van-col span="16"><keyboard @hide="complete" /></van-col>
        </van-row>
        <van-row style="text-align:center;line-height:60px">
            <van-col span="8" style="font-size:18px">备 注</van-col>
            <van-col span="16"><van-field name="备注" v-model="message" style="margin-top:8px;" placeholder="请输入备注"/></van-col>
        </van-row>
        <div style="margin:16px;">
            <van-button round block type="info" @click="onSubmit">确认</van-button>
        </div>
    </div>
</template>

<script>
import Calender from '../../components/Calender.vue';
import Keyboard from '../../components/Keyboard.vue';
import Kinds from '../../components/Kinds.vue';
import { Dialog } from 'vant';
import storage from '../../router/storage.js';
import { Toast } from 'vant';

export default {
    name:'Add',
    components: { Calender, Keyboard, Kinds,  },
    data(){
        return{
            val1: "",
            val2: "",
            val3: "",
            message: '',
            radio: '1',
        }
    },
    methods:{
        onSubmit(){
            if(this.val1 == ''){
                Dialog.alert({
                message: '日期没有填写!',
                }).then(() => {
                // on close
                });
               
            }else if(this.val2 == ''){
                Dialog.alert({
                message: '消费项目没填!',
                }).then(() => {
                // on close
                });
              
            }else if(this.val3 == ''){
                Dialog.alert({
                message: '消费金额没填',
                }).then(() => {
                // on close
                });
               
            }
            else{
                var user = storage.get('name');
                var list = {
                    user: user,
                    kind: this.radio,
                    date:this.val1,
                    item:this.val2,
                    amount:this.val3,
                    msg:this.message
                }
                var data = [];
                if(storage.get('goods')==null){
                    data.push(list)
                    storage.set('goods',data);
                }else{
                    data = storage.get('goods');
                    data.push(list);
                    storage.set('goods',data);
                }
                this.$router.push('/main/home');
                Toast.success('成功记一笔');
            }
        },
        onConfirm(val){
            this.val1 = val;
        },
        onChange(val) {
            this.val2 = val;
        },
        complete(data){
            this.val3 = data.value;
        }
    }
}
</script>

<style scoped>
#head{
    display: flex;
    justify-content: center;
    line-height: 40px;
    padding: 20px 0;
}
</style>