<template>
    <div>
        <p>用户信息管理</p>
        <van-form @submit="onSubmit">
          <van-field
            v-model="username"
            name="用户名"
            label="用户名"
          />
          <van-field
            v-model="password"
            name="密 码"
            label="密 码"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">提交修改</van-button>
          </div>
        </van-form>
        
        <p>选项条目管理</p>
        <kinds />
         <van-form @submit="onSubmit2">
          <van-field
            v-model="additem"
            name="添加项目"
            label="添加项目"
          />
          <van-field
            v-model="deleteitem"
            name="删除项目"
            label="删除项目"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">提交修改</van-button>
          </div>
        </van-form>
        <p>收入支出管理</p>
        <div id="ech">
          <div id="main" style="width: 360px;height:240px;"></div>
        </div>
        <div id="data">
          <h3>统计分析</h3>
          共收入{{this.m1}}元，共支出{{this.m2}}元
        </div>
        
        <p style="height:120px;"></p>
    </div>
</template>

<script>
import Kinds from '../../components/Kinds.vue'
import storage from '../../router/storage.js'
let echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/pie')
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
import { Toast } from 'vant';

export default {
    name:'Set',
    components:{Kinds},
    data(){
      return{
        username:'',
        password:'',
        additem:'',
        deleteitem:'',
        flag1: 0,
        flag2: 0,
        m1:0,
        m2:0,
        lists:[],
        userlists:[],
      }
    },
    mounted(){
      var data = storage.get('userinfo');
      var user = storage.get('name');
      this.username = user;
      for(var i=0;i<data.length;i++){
        if(user == data[i].username){
          this.password = data[i].password1;
        }
      }
      if(storage.get('goods')==null){
            console.log("数据为空");
        }else{
            this.userlists = storage.get('goods');
            var user1 = storage.get('name');
            for(var k=0;k<this.userlists.length;k++){
                if(user1 == this.userlists[k].user){
                    this.lists.push(this.userlists[k])
                }
            }
            this.number = this.lists.length;
            for(var j=0;j<this.lists.length;j++){
                if(this.lists[j].kind == '1'){
                    this.flag1++;
                    this.m1 = parseInt(this.m1)+parseInt(this.lists[j].amount);
                }else{
                    this.flag2++;
                    this.m2 = parseInt(this.m2)+parseInt(this.lists[j].amount);
                }
            }
      }
      var myChart = echarts.init(document.getElementById('main'));
      var option = {
          title: {
              left: 'center'
          },
          tooltip: {
              trigger: 'item',
              formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          series: [
              {
                  type: 'pie',
                  radius: '55%',
                  center: ['50%', '60%'],
                  data: [
                      {value: this.flag1, name: '收入'+this.flag1+'项'},
                      {value: this.flag2, name: '支出'+this.flag2+'项'},
                      
                  ],
                  emphasis: {
                      itemStyle: {
                          shadowBlur: 10,
                          shadowOffsetX: 0,
                          shadowColor: 'rgba(0, 0, 0, 0.5)'
                      }
                  }
              }
          ]
      };
      myChart.setOption(option);
    },
    methods:{
      onSubmit(){
        var data = storage.get('userinfo');
        var itm = storage.get('name');
        for(var i=0;i<data.length;i++){
          if(itm == data[i].username){
              data.splice(i,1);
          }
        }
        var user ={
          username:this.username,
          password1:this.password,
          password2:this.password,
        }
        data.push(user);
        storage.set('userinfo',data);
        Toast.success('修改成功');
      },
      onSubmit2(){
        var pro ={
          add:this.additem,
          delete:this.deleteitem
        }
        storage.set('project',pro);
        this.$router.go(0);
        Toast.success('修改成功');
      }
    }
}
</script>

<style scoped>
p{
  width: 100%;
  text-align: center;
  line-height: 50px;
  font-size: 24px;
}
#ech{
  text-align: center;
}
#data{
  width: 100%;
  text-align: center;
  font-size: 20px;
}
</style>