<template>
  <div>
        <van-swipe-cell>
            <!-- <van-card
                :num="msg"
                :price="amount"
                :desc="date"
                :title="item"
                class="goods-card"
                thumb="../assets/shouru.png"
            /> -->
            
            <div id="com">
                <img src="../assets/shouru.png" class="img" v-show="show==1">
                <img src="../assets/zhichu.png" class="img" v-show="show==2">
                <div class="content">
                    <span id="t1">{{item}}</span>
                    <div id="box">
                        <div id="box2">
                            <span class="t3">{{amount}}元</span>
                            <span class="t2">{{date}}</span>
                        </div>
                        <span class="t4"><span class="t5">备注:</span>{{msg}}</span>
                    </div>
                  
                </div>
            </div>
            <template #right>
                <van-button square text="删除" @click="deleteItem" type="danger" class="delete-button" />
            </template>
        </van-swipe-cell>
        
    </div>
</template>

<script>
import storage from '../router/storage.js'
import { Toast } from 'vant';
export default {
    name:'Item',
    data(){
        return{
            dateitem:"",
            show: 1,
        }    
    },
    props:{
        'index': Number,
        'date': String,
        'item': String,
        'amount': String,
        'msg': String,
        'kind': String,
        'user': String,
    },
    methods:{
        deleteItem(){
            var datas = storage.get('goods');
            for(var i=0;i<datas.length;i++){
                if(this.user == datas[i].user&&this.date == datas[i].date&&this.amount == datas[i].amount&&this.item == datas[i].item&&this.msg == datas[i].msg){
                    datas.splice(i,1);
                }
            }
            storage.set('goods',datas);
            this.$router.go(0);
            Toast.success('删除成功');
        }
    },
    mounted(){
        if(this.kind == '1'){
            this.show = 1;
        }else{
            this.show = 2;
        }
    }
}
</script>

<style scoped>
.goods-card {
    margin: 0;
    background-color: #F0F4F2;
}
.delete-button {
    height: 100%;
}
#com{
    height: 80px;
    display: flex;
    background:#ffffff;
    box-shadow: 0 0 1px grey inset;
}
.img{
    width: 50px;
    height: 50px;
    padding: 15px;
}
.content{
    padding: 10px 10px;
    width: 100%;
    display: flex;
    justify-content: center;
}
#t1{
    font-size:20px;
    font-weight: bold;
    width: 36px;
    padding-top: 4px;
}
#box{
    width: 100%;
    display: flex;
    flex-direction: column;
}
#box2{
    padding-left: 15px;
    padding-bottom: 16px;
    display: flex;
    justify-content: space-between;
}
.t2{
    font-size: 16px;
    text-align: right;
    padding-top: 3px;
}
.t3{
    font-size:18px;
    font-weight: bold;
}
.t4{
    padding-left: 15px;
    font-size: 14px;
    color: rgb(117, 114, 114);
}
</style>