<template>
  <div>
      <van-field
      placeholder="请输入金额"
  readonly
  clickable
  :value="value"
  @touchstart.native.stop="show = true"
/>
<van-number-keyboard
  v-model="value"
  :show="show"
  :maxlength="6"
  @blur="show = false"
  @hide="complete(value)"
/>
     
  </div>
</template>

<script>
export default {
    name:'Keyboard',
    data() {
        return {
            show: false,
            value: '',
        };
    },
    methods:{
        complete(val){
            let data = {
              value: val
            };
            this.$emit('hide',data);
        }
    }
}
</script>

