<template>
  <div>
        <van-cell title="选择日期" style="font-size:18px;" :value="date" @click="show = true"/>
        <van-calendar v-model="show" color="#1989fa" :min-date="minDate"  @confirm="onConfirm" />
  </div>
</template>

<script>
export default {
    name:'Calender',
    data() {
        return {
            date: '',
            show: false,
            minDate: new Date(2000,1,1),
        };
    },
    methods: {
        formatDate(date) {
            return `${date.getYear()+1900}/${date.getMonth() + 1}/${date.getDate()}`;
        },
        onConfirm(date) {
            this.show = false;
            this.date = this.formatDate(date);
            this.$emit('confirm',this.date);
        },
    },
}
</script>

