<template>
  <div id="register">
      <van-form @submit="onSubmit" >
          <div id="icon"><img src="../assets/register.png"></div>
            <van-field
            style="font-weight:bold;font-size:20px;"
                v-model="username"
                name="username"
                label="用户名"
                placeholder="用户名"
                :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
            style="font-weight:bold;font-size:20px;"
                v-model="password1"
                type="password"
                name="password1"
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />
             <van-field
             style="font-weight:bold;font-size:20px;"
                v-model="password2"
                type="password"
                name="password2"
                label="确认密码"
                placeholder="确认密码"
                :rules="[{ required: true, message: '请确认密码' }]"
            />
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">提交</van-button>
            </div>
            <div id="buttom">
                <p><router-link to="/login" style="color:black;font-size:18px;">返回登录</router-link></p>
            </div>
        </van-form>
  </div>
</template>

<script>
import { Dialog } from 'vant';
import storage from '../router/storage.js';
export default {
    name:'Register',
        data() {
            return {
                username: '',
                password1: '',
                password2: ''
            };
        },
        methods: {
            onSubmit(values) {
                if(this.password1!=this.password2){
                    Dialog.alert({
                    message: '两次密码不⼀致!',
                    }).then(() => {
                    // on close
                    });
                }else{
                    var data = [];
                    if(storage.get('userinfo')==null){
                        data.push(values)
                        storage.set('userinfo',data);
                    }else{
                        data = storage.get('userinfo');
                        var flag = false;
                        for(var i = 0; i < data.length;i++){
                            if(values.username == data[i].username){
                                flag = true;
                            }
                        }
                        if(flag){
                            Dialog.alert({
                            message: '该用户已被注册！',
                            }).then(() => {
                            // on close
                            });
                        }else{
                            data.push(values);
                            storage.set('userinfo',data);
                        }
                    }
                    this.$router.push('/login');
                }

            }
        }
}
</script>

<style>
#register{
    margin-top: 140px;
}
#icon{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}
#buttom{
    display: flex;
    justify-content: center;
    
}
</style>